import React, {Component} from 'react';
import Navbar from "react-bootstrap/Navbar";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';


export default class DownloadSpeedChartSample extends Component {

  load_chart_options() {
    return {
      chart: {
        type: 'scatter',
        height: 150
      },
      title: {
        text: 'Stacked bar chart'
      },
      tooltip: {
        headerFormat: "",
        pointFormat: "{series.name}: <b>{point.x}</b><br/>"
      },
      xAxis: {
        tickInterval: 5,
        categories: []
      },
      yAxis: {
        min: 0.5,
        max: 1.5,
        gridLineWidth: 0,
        tickAmount: 1,
        title: {
          text: 'Download speed',
          rotation: 0,
        },
        labels: {
          enabled: false
        }
      },
      legend: {
        enabled: false,
      },
      plotOptions: {
        series: {
          stacking: 'normal'
        }
      },
      series: [{
        name: 'John',
        data: [
          [56.12, 1],
          [52.12, 1],
          [34.12, 1],
          [61.12, 1],
          [53.12, 1],
        ]
      }]
    }

  }

  constructor(props) {
    super(props);
    this.chartComponent = React.createRef();
  }

  render() {
    return (
      <>
        <Navbar bg="light">
          <Navbar.Brand href="#">KNP</Navbar.Brand>
        </Navbar>
        <Container>
          <Row>
            <Col>
              Hello, World!
              <Card>
                <Card.Body>
                  <HighchartsReact
                    ref={this.chartComponent}
                    highcharts={Highcharts}
                    options={this.load_chart_options()}/>

                </Card.Body>
              </Card>
            </Col>
          </Row>
          <Row>
            <Button onClick={this.redrawChart.bind(this)}>Redraw</Button> {' '}
            <Button onClick={this.addPoint.bind(this)}>Add Point</Button> {' '}
          </Row>
        </Container>
      </>
    );
  }

  addPoint() {
    const chart = this.chartComponent.current.chart;
    chart.series[0].addPoint([1514605600000, 2]);
  }

  redrawChart() {
    const chart = this.chartComponent.current.chart;
    chart.redraw();
  }

  componentDidMount() {
    const chart = this.chartComponent.current.chart;
    return;
    fetch('https://cdn.jsdelivr.net/gh/highcharts/highcharts@v7.0.0/samples/data/usdeur.json')
      .then(res => res.json())
      .then(data => {
        console.log(this.chartComponent);
        console.log('data', data);
        chart.series[0].setData(data);
        chart.redraw();
      })
  }
}
