import React, {Component} from 'react';
import Table from "react-bootstrap/Table";
import Form from "react-bootstrap/Form";
import InputGroup from "react-bootstrap/InputGroup";
import Button from "react-bootstrap/Button";
import DownloadScenario from "../scenarios/download_scenario";
import UploadScenario from "../scenarios/upload_scenario";
import WaitScenario from "../scenarios/wait_scenario";
import ScenarioCSVManager from "./scenario_csv_manager";


export default class ScenarioTable extends Component {
  async edit(scenario) {
    let editing = !this.state.scenarios[scenario.id].editing;
    this.state.scenarios[scenario.id].editing = editing;
    await this.sync_table();
  }

  async sync_table(){
    await this.setState({
      scenarios: this.state.scenarios
    });
    await this.props.table_updated(this.state.scenarios);
  }

  async add() {
    this.state.scenarios.push(
      this.normalize_scenario(new DownloadScenario(20, 1<<17, 1, null))
    );
    this.reindex_scenarios();
    await this.sync_table();
  }

  async type_changed(scenario, event) {
    let class_lookup = {
      [WaitScenario.TYPE]: WaitScenario,
      [DownloadScenario.TYPE]: DownloadScenario,
      [UploadScenario.TYPE]: UploadScenario
    };
    let new_scenario = new class_lookup[event.target.value]();
    new_scenario.num_measurements = scenario.num_measurements || 0;
    new_scenario.num_streams = scenario.num_streams || 0;
    new_scenario.file_size = scenario.file_size || 0;
    new_scenario = this.normalize_scenario(new_scenario);
    new_scenario.editing = true;
    this.state.scenarios[scenario.id] = new_scenario;
    await this.reindex_scenarios();
    await this.sync_table();
  }

  async delete_row(scenario) {
    console.log("scenario id", scenario.id);
    this.state.scenarios = this.state.scenarios.filter((_, i) => i !== scenario.id);
    this.reindex_scenarios();
    await this.sync_table();
  }

  reindex_scenarios() {
    let count = 0;
    this.setState({
      scenarios: this.state.scenarios.map((s) => {
        s.id = count++;
        return s;
      })
    });
  }

  constructor(props) {
    super(props);
    this.state = {
      scenarios: this.normalize_scenarios(props.scenarios)
    };
    this.scenario_types = [
      DownloadScenario.TYPE,
      UploadScenario.TYPE,
      WaitScenario.TYPE
    ]
  }

  normalize_scenario(scenario) {
    let num = scenario.file_size;
    scenario.size_formatted = (scenario.type === WaitScenario.TYPE)?
      {value: 0, postfix: "B"}:
      this.get_formatted_measurement(num);
      this.update_size(scenario);
    return scenario;
  }

  normalize_scenarios(scenarios) {
    return scenarios.map(scenario => {
      return this.normalize_scenario(scenario);
    })
  }

  get_formatted_measurement(size) {
    if (Math.floor(size/(1<<20)) !== 0) {
      return {
        value: size/(1<<20),
        postfix: "MB"
      };
    }
    if (Math.floor(size/(1<<10)) !== 0) {
      return {
        value: size/(1<<10),
        postfix: "KB"
      };
    }
    return {
      value: size,
      postfix: "B"
    };
  }

  update_size(scenario) {
    let value_lookup = {
      "KB": 1<<10,
      "MB": 1<<20,
      "B": 1
    };
    scenario.label = `${scenario.size_formatted.value}${scenario.size_formatted.postfix}`;
    scenario.file_size =
      value_lookup[scenario.size_formatted.postfix]
      * scenario.size_formatted.value;
  }

  async csv_imported(scenarios) {
    this.state.scenarios = this.normalize_scenarios(scenarios);
    this.reindex_scenarios();
    await this.sync_table();
  }

  render() {
    return(
      <>
        <Table striped bordered responsive>
          <thead>
          <tr>
            <th>#</th>
            <th>Type</th>
            <th># Bytes</th>
            <th># Measurements</th>
            <th># Streams</th>
            <th>Delete</th>
          </tr>
          </thead>
          <tbody>
          {this.state.scenarios.map(scenario => (
            <tr key={scenario.id}>
              <td>{scenario.id + 1}</td>
              { scenario.editing ?
                (<>
                  <td>
                    <Form.Group controlId="scenarioType">
                      <Form.Control as="select"
                                    defaultValue={scenario.type}
                                    onChange={(value)=>{this.type_changed(scenario, value)}}>
                        {this.scenario_types.map(type =>
                          <option value={type} key={type}>{type}</option>
                        )}
                      </Form.Control>
                    </Form.Group>
                  </td>
                  {
                    (scenario.type === 'Wait') ?
                      <>
                        <td colSpan={3}>
                          <InputGroup>
                            <Form.Control
                              type="number"
                              placeholder="Wait amount (in milliseconds)"
                              aria-describedby="waitAmount"
                              name="size"
                              onChange={(e)=> {scenario.duration = parseInt(e.target.value)}}
                              defaultValue={scenario.duration}
                            />
                            <InputGroup.Append>
                              <InputGroup.Text>ms</InputGroup.Text>
                            </InputGroup.Append>
                          </InputGroup>
                        </td>
                      </> :
                      <>
                        <td>
                          <Form.Group
                            controlId="size"
                            style={{width:170}}
                          >
                            <InputGroup>
                              <Form.Control
                                type="number"
                                placeholder="Size"
                                name="size"
                                defaultValue={scenario.size_formatted.value}
                                onChange={e => {
                                  scenario.size_formatted.value = e.target.value;
                                  this.update_size(scenario);
                                }}
                              />
                              <InputGroup.Append>
                                <Form.Control
                                  as="select"
                                  defaultValue={scenario.size_formatted.postfix}
                                  onChange={(e)=>{
                                    scenario.size_formatted.postfix = e.target.value;
                                    this.update_size(scenario);
                                  }}>
                                  {['B', 'KB', 'MB'].map(type =>
                                    <option value={type} key={type}>{type}</option>
                                  )}
                                </Form.Control>
                              </InputGroup.Append>
                            </InputGroup>
                          </Form.Group>
                        </td>
                        <td>
                          <Form.Group
                            style={{width:100}}
                          >
                            <InputGroup>
                              <Form.Control
                                type="number"
                                aria-describedby="numberOfMeasurements"
                                name="num_measurements"
                                defaultValue={scenario.num_measurements}
                                onChange={e => {scenario.num_measurements = parseInt(e.target.value)}}
                              />
                            </InputGroup>
                          </Form.Group>
                        </td>
                        <td>
                          <Form.Group
                            style={{width:100}}
                          >
                            <InputGroup>
                              <Form.Control
                                type="number"
                                aria-describedby="numberOfStreams"
                                name="num_streams"
                                defaultValue={scenario.num_streams}
                                onChange={e => {scenario.num_streams = parseInt(e.target.value)}}
                              />
                            </InputGroup>
                          </Form.Group>
                        </td>
                      </>
                  }
                </>) :
                (
                  <>
                    {
                      (scenario.type === 'Wait') ?
                        <>
                          <td colSpan={4}>{`${scenario.type} ${scenario.duration}ms`}</td>
                        </> :
                        <>
                          <td>{scenario.type}</td>
                          <td>{`${scenario.size_formatted.value} ${scenario.size_formatted.postfix}`}</td>
                          <td>{scenario.num_measurements}</td>
                          <td>{scenario.num_streams}</td>
                        </>
                    }
                  </>
                )
              }
              <td>
                <Button variant="success" size="sm" type="submit" onClick={()=>{this.edit(scenario)}}>
                  {scenario.editing ? 'Done' : 'Edit'}
                </Button>
                {' '}
                <Button variant="danger" size="sm" onClick={() => {this.delete_row(scenario)}}>Delete</Button>
              </td>
            </tr>
          ))}
          </tbody>
        </Table>
        <Button variant="success" onClick={this.add.bind(this)}>Add Row</Button>
        {' '}
        <ScenarioCSVManager
          scenarios={this.state.scenarios}
          csv_imported={async (scenarios) => { await this.csv_imported(scenarios) }}
        />
      </>
    )
  }
}
