export default class ColourProvider {
  static DEFAULT_COLOURS = [ '#0d233a', '#2f7ed8', '#8bbc21', '#910000', '#1aadce',
    '#492970', '#f28f43', '#77a1e5', '#c42525', '#a6c96a'];
  static mapping = {};
  static count = 0;

  static get_colour(id) {
    let num_colours = this.DEFAULT_COLOURS.length;
    if (!(id in this.mapping)) {
      this.mapping[id] = this.count;
      this.count += 1 ;
    }
    let index = this.mapping[id];
    return this.DEFAULT_COLOURS[index % num_colours];
  }
}

