import AppConfiguration from "../config";
import BandwidthScenario from "./bandwidth_scenario";

export default class DownloadScenario extends BandwidthScenario {
  static get TYPE() {
    return "Download"
  }

  constructor(...options) {
    super(...options);
    this.type = 'Download';
  }

  static get DOWN_ENDPOINT() {
    return (AppConfiguration.CONFIG === "arshan") ?
      "/hello/down":
      "__down";
  }

  async measure() {
    return this.download();
  }

  async download() {
    const streams = this.num_streams;
    const url = `${BandwidthScenario.BASE_URL}/${DownloadScenario.DOWN_ENDPOINT}`;
    let signal = this.controller.signal;
    let requests = Array(streams)
      .fill(0)
      .map(r => new Object())
      .map((req) => {
        req.start = Date.now();
        req.fetch = fetch(`${url}?bytes=${this.file_size}`, {signal});
        return req;
      });
    await Promise.all(requests.map(req => req.fetch.then(res => res.text().then(t => {
      req.finish = Date.now();
    }))));
    let rates = requests.map(r=>(this.file_size)/(r.finish - r.start));
    return this.convert_to_mbps(rates.reduce((a, b) => a + b));
  }
}

