import React, {Component} from 'react';
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import WaitScenario from "../scenarios/wait_scenario";
import DownloadScenario from "../scenarios/download_scenario";
import UploadScenario from "../scenarios/upload_scenario";
import { saveAs } from 'file-saver';


export default class ScenarioCSVManager extends Component {
  export_as_csv() {
    let header_row = ["type", "num_bytes", "num_measurements", "num_streams", "duration"]
    let csv_content = "";
    csv_content += header_row.join(",") + "\r\n";
    this.props.scenarios.forEach(s => {
      let row = (s.type === WaitScenario.TYPE) ?
        [s.type, "", "", "", s.duration] :
        [s.type, s.file_size, s.num_measurements, s.num_streams, ""];
      csv_content += row.join(",") + "\r\n";
    });
    let file = new File(
      [csv_content],
      "scenarios.csv",
      {type: "text/plain;charset=utf-8"});
    saveAs(file);
  }

  import_csv(e) {
    if (e.target.files.length === 0) {
      return;
    }
    let class_lookup = {
      [WaitScenario.TYPE]: WaitScenario,
      [DownloadScenario.TYPE]: DownloadScenario,
      [UploadScenario.TYPE]: UploadScenario
    };
    let fr = new FileReader();
    fr.onload = () => {
      let lines = fr.result.split("\r\n").splice(1);
      let scenarios = lines.map(l => {
        let attrs = l.split(",");
        if (attrs.length < 5) {
          attrs = attrs.concat("0".repeat(5 - attrs.length).split(""));
        }
        if (!(attrs[0] in class_lookup)) {
          return ;
        }
        let new_scenario = new class_lookup[attrs[0]]();
        new_scenario.file_size = parseInt(attrs[1]) || 0;
        new_scenario.num_measurements = parseInt(attrs[2]) || 0;
        new_scenario.num_streams = parseInt(attrs[3]) || 0;
        new_scenario.duration = parseInt(attrs[4]) || 0;
        return new_scenario;
      }).filter(s => s);
      this.props.csv_imported(scenarios);
    };
    fr.readAsText(e.target.files[0]);
  }


  render() {
    return (
      <>
        <Button
          onClick={this.export_as_csv.bind(this)}
        >
          Export
        </Button>
        {' '}
        <Form>
          <Form.Group>
            <Form.File
              onChange={this.import_csv.bind(this)}
              label="Load a CSV file" />
          </Form.Group>
        </Form>
      </>
    );
  }
}
