import React, {Component} from 'react';
import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';
import Card from "react-bootstrap/Card";
import ColourProvider from "../utilities/colours";

export default class BandwidthChart extends Component {
  chart_options = {
    chart: {
      type: 'scatter',
      height: 100
    },
    tooltip: {
      headerFormat: "",
      pointFormat: "{series.name}: <b>{point.x}</b><br/>"
    },
    xAxis: {
      tickInterval: 5,
      categories: []
    },
    title: {
      floating: true,
      text: ""
    },
    yAxis: {
      width: 0,
      min: 0.7,
      max: 1.8,
      gridLineWidth: 0,
      tickAmount: 1,
      title: {
        reserveSpace: false,
        text: '',
        rotation: 0,
        x: 40,
        y: -15,
      },
      labels: {
        enabled: false
      }
    },
    legend: {
      enabled: false,
    },
    plotOptions: {
      series: {
        stacking: 'normal'
      }
    },
    series: [{
      name: 'Download Speed',
      data: [],
      color: 'red'
    }]
  };

  constructor(props) {
    super(props);
    this.chartComponent = React.createRef();
    this.chart_options.series[0] = {
      name: this.props.series_name,
      data: [],
      color: ColourProvider.get_colour(this.props.color_id)
    }
  };

  render() {
    return (
      <>
        <Card>
          <Card.Body>
            <Card.Title>{this.props.title}</Card.Title>
            <HighchartsReact
              ref={this.chartComponent}
              highcharts={Highcharts}
              options={this.chart_options}/>
          </Card.Body>
        </Card>
      </>
    );
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (!prevProps.data) {
      return;
    }
    const datum = prevProps.data.slice(-1).pop();
    if (!datum) {
      return;
    }
    const chart = this.chartComponent.current.chart;
    if (chart.series[0].data.length === prevProps.data.length) {
      return;
    }
    chart.series[0].addPoint([parseFloat(datum.rate.toFixed(2)), 1]);
  }
}
