import React, {Component} from 'react';
import Navbar from "react-bootstrap/Navbar";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Button from "react-bootstrap/Button";
import DownloadScenario from "../scenarios/download_scenario";
import WaitScenario from "../scenarios/wait_scenario";


export default class DownloadScenarioSample extends Component {
  constructor(props) {
    super(props);
    this.create_scenario();
  }

  render() {
    return (
      <>
        <Navbar bg="light">
          <Navbar.Brand href="#">KNP</Navbar.Brand>
        </Navbar>
        <Container>
          <Row>
            <Button onClick={this.start_download.bind(this)}>Start</Button>
            <Button onClick={this.pause_download.bind(this)}>Pause</Button>
          </Row>
        </Container>
      </>
    );
  }

  create_scenario () {
    this.scenario = new WaitScenario(2*1000);
    //this.scenario = new DownloadScenario(10, 1<<20, '1MB');
  }

  async start_download() {
    if (await this.scenario.run()) {
      console.log('Finished!');
    }
  }

  pause_download() {
    this.scenario.pause();
  }
}
