import Scenario from "./scenario";

export default class WaitScenario extends Scenario {
  static get TYPE() {
    return "Wait"
  }

  constructor(duration) {
    super();
    this.duration = duration;
    this.last_start = null;
    this.type = "Wait";
    this.started = false;
  }

  async wait(time_ms) {
    return new Promise(resolve => {
      this.resolver = resolve;
      this.last_start = Date.now();
      this.timer = setTimeout(() => {
        resolve(true);
      }, time_ms);
    });
  }

  async run() {
    super.run();
    if (!this.started) {
      this.amount_remaining = this.duration;
      this.started = true;
    }
    return await this.wait(this.amount_remaining);
  }

  pause() {
    super.pause();
    clearTimeout(this.timer);
    this.amount_remaining -= (Date.now() - this.last_start);
    console.log(`paused, amount remaining: ${this.amount_remaining}`);
    this.resolver(false);
  }
}


