import React, {Component} from 'react';


export default class MultipleUrlsSample extends Component {
  render() {
    return (
      <div className="App">
        <button onClick={this.startDownloadMultipleURLs.bind(this)}>multiple URLs!</button>
      </div>
    );
  }

  convertToMbps(r) {
    return r*1000/(1<<20)*8;
  }

  logRate(r) {
    console.log(`${this.convertToMbps(r).toFixed(2)}Mbps`);
  }

  async startDownloadMultipleURLs() {
    let urls = [
      "https://worker.arshankhanifar.workers.dev/hey/down",
      "https://speed.cloudflare.com/__down"
    ];
    const bytes = 1 << (20);
    await this.downloadFromMultipleUrls(bytes, urls);
  }

  async downloadFromMultipleUrls (bytes, urls) {
    let requests = urls.map(url => {return {url: url} })
      .map((req) => {
        req.start = Date.now();
        req.fetch = fetch(`${req.url}?bytes=${bytes}`);
        return req;
      });
    await Promise.all(requests.map(req => req.fetch.then(res => res.text().then(t => {
      req.finish = Date.now();
    }))));
    let rates = requests.map(r=>(bytes)/(r.finish - r.start));
    let total_rate = rates.reduce((a, b) => a + b);
    this.logRate(total_rate);
    //return total_rate;
  }

}
