import React, {Component} from 'react';


export default class SingleStreamSample extends Component {
  static get CONFIG() {
    return "arshan";
    return "cloudflare";
  }

  static get BASE_URL() {
    return (this.CONFIG === "arshan") ?
      "https://worker.arshankhanifar.workers.dev":
      "https://speed.cloudflare.com";
  }
  static get DOWN_ENDPOINT() {
    return (this.CONFIG === "arshan") ?
      "/hello/down":
      "__down";
  }

  render() {
    return (
      <div className="App">
        <button onClick={this.startDownload.bind(this)}>click me!</button>
      </div>
    );
  }

  convertToMbps(r) {
    return r*1000/(1<<20)*8;
  }

  logRate(r) {
    console.log(`${this.convertToMbps(r).toFixed(2)}Mbps`);
  }

  async download(bytes, streams, url) {
    let requests = Array(streams)
      .fill(0)
      .map(r => {return {}})
      .map((req) => {
        req.start = Date.now();
        req.fetch = fetch(`${url}?bytes=${bytes}`);
        return req;
      });
    await Promise.all(requests.map(req => req.fetch.then(res => res.text().then(t => {
      req.finish = Date.now();
    }))));
    let rates = requests.map(r=>(bytes)/(r.finish - r.start));
    let total_rate = rates.reduce((a, b) => a + b);
    this.logRate(total_rate);
    return total_rate;
  }

  async startDownload() {
    let url = `${SingleStreamSample.BASE_URL}/${SingleStreamSample.DOWN_ENDPOINT}`;
    let test = async (bytes, streams, n) => {
      let rates = [];
      for (let i = 0; i < n; i++ ) {
        rates.push(await this.download(bytes, streams, url));
      }
      console.log(`Avg: ${this.convertToMbps(rates.reduce((a, b) => a + b)/rates.length).toFixed(2)}Mbps`)
    };
    const bytes = 1 << (24); // 16MB
    await test(bytes, 6, 1);
    //await test(bytes, 2, 20);
  }
}
