import AppConfiguration from "../config";
import BandwidthScenario from "./bandwidth_scenario";

export default class UploadScenario extends BandwidthScenario {
  static get TYPE() {
    return "Upload"
  }

  constructor(...options) {
    super(...options);
    this.type = 'Upload';
  }

  static get UP_ENDPOINT() {
    return (AppConfiguration.CONFIG === "arshan") ?
      "/hello/up":
      "__up";
  }

  async measure() {
    return this.upload();
  }

  async upload() {
    const streams = this.num_streams;
    const url = `${BandwidthScenario.BASE_URL}/${UploadScenario.UP_ENDPOINT}`;
    let signal = this.controller.signal;
    let requests = Array(streams)
      .fill(0)
      .map(r => new Object())
      .map((req) => {
        req.start = Date.now();
        req.fetch = fetch(`${url}`, {...{
          method: 'POST',
          body: '0'.repeat(this.file_size),
          headers: { 'content-length': this.file_size }
        }, signal});
        return req;
      });
    await Promise.all(requests.map(req => req.fetch.then(res => res.text().then(t => {
      req.finish = Date.now();
    }))));
    let rates = requests.map(r=>(this.file_size)/(r.finish - r.start));
    return this.convert_to_mbps(rates.reduce((a, b) => a + b));
  }
}

