import React, {Component} from 'react';
import Chart from 'chart.js';
import './ping_sample.scss';


const INTERVAL_MS = 400;


export default class PingSample extends Component {
  constructor(props) {
    super(props);
    this.state = {
      measuring: false,
      latest_measurement: 0
    };
    this.timer = null;
  }

  render() {
    return (
      <div className="App">
        <h1>my chart</h1>
        <canvas id="myChart" width="400" height="400"/>
        <br/>
        <button onClick={this.start.bind(this)}>{this.state.measuring ? 'Pause' : 'Start'}</button>
        <br/>
        <span>latency: {this.state.latest_measurement}</span>
      </div>
    );
  }

  start() {
    if (this.state.measuring) {
      clearTimeout(this.timer);
    } else {
      this.measure(INTERVAL_MS);
    }
    this.setState({
      measuring: !this.state.measuring
    });
  }

  measure(interval) {
    let start = Date.now();
    fetch("https://api.github.com/users/arshankhanifar")
      .then(res => res.json())
      .then(res => {
        let end = Date.now();
        console.log(`latency: ${end - start}ms`);
        let latency = end - start;
        this.setState({
          latest_measurement: latency
        });
        this.addChartPoint(latency, this.chart.data.labels.length + 1);

      });
    this.timer = setTimeout(() => this.measure(interval), interval);
  }

  triggerAddPoint() {
    this.addChartPoint(Math.random()*10,2);
  }

  addChartPoint(y, x) {
    console.log(this.chart.data);
    this.chart.data.datasets[0].data.push(y);
    this.chart.data.labels.push(x);
    this.chart.update();
  }

  componentDidMount() {
    this.setState({
      latest_measurement: 0
    });
    this.chart = this.loadChart();
  }

  loadChart() {
    var ctx = document.getElementById('myChart').getContext('2d');
    return new Chart(ctx, {
      type: 'line',
      data: {
        datasets: [{
          label: "latency",
          backgroundColor: 'rgba(33, 150, 243, 0.8)',
          data: []
        }],
        labels: []
      },
      options: {
        responsive: false,
        scales: {
          yAxes: [{
            ticks: {
              min: 0
            }
          }],
          xAxes: [{
            ticks: {
              min: '1'
            }
          }]
        }
      }
    });
  }
}
