import React, {Component} from 'react';
import Navbar from "react-bootstrap/Navbar";
import Container from "react-bootstrap/Container";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import Button from "react-bootstrap/Button";
import PingChart from "./components/ping_chart";
import BandwidthChart from "./components/bandwidth_chart";
import WaitScenario from "./scenarios/wait_scenario";
import DownloadScenario from "./scenarios/download_scenario";
import AppConfiguration from "./config";
import UploadScenario from "./scenarios/upload_scenario";
import ScenarioTable from "./components/scenario_table";


const MEASUREMENTS = {
  DOWNLOAD: "download_measurements",
  UPLOAD: "upload_measurements",
};


export default class App extends Component {
  reset_values() {
    return {
      started: false,
      paused: true,
      current_idx: 0,
      [MEASUREMENTS.DOWNLOAD]: {},
      [MEASUREMENTS.UPLOAD]: {},
    }
  }

  constructor(props) {
    super(props);
    this.state = this.reset_values();
    this.ping_timer = null;
    this.populate_scenarios();
    this.ping_chart = React.createRef();
  }

  populate_scenarios() {
    this.state.scenarios = [
      new WaitScenario(500),
      new DownloadScenario(20, 1<<17, 1, this.collect_bandwidth_data.bind(this)),
      new DownloadScenario(20, 1<<20, 1, this.collect_bandwidth_data.bind(this)),
      new DownloadScenario(20, 10*(1<<20), 1, this.collect_bandwidth_data.bind(this)),
      new WaitScenario(500),
      new UploadScenario(20, 1<<17, 1, this.collect_bandwidth_data.bind(this)),
      new WaitScenario(500),
      new UploadScenario(20, 1<<20, 1, this.collect_bandwidth_data.bind(this)),
      new UploadScenario(20, 10*(1<<20), 1, this.collect_bandwidth_data.bind(this)),
      new WaitScenario(500),
    ];
    this.reindex_scenarios();
  }

  reindex_scenarios() {
    let count = 0;
    this.state.scenarios = this.state.scenarios.map((s) => {
      s.id = count++;
      return s;
    });
  }

  async table_updated(scenarios) {
    console.log("table has been now updated!!!!", scenarios);
    await this.setState({
      scenarios: scenarios.map(s => {
        if (s.type !== WaitScenario.TYPE) {
          s.result_callback = this.collect_bandwidth_data.bind(this)
        }
        return s;
      })
    });
  }

  render() {
    return (
      <>
        <Navbar bg="light">
          <Navbar.Brand href="#">KNP</Navbar.Brand>
        </Navbar>
        <Container>
          <Row>
            <Col lg={12}>
              <h1>The Khanifar Bufferbloat Test</h1>
              <p>Best buffer bloat test in the history of mankind, ever</p>
            </Col>
            <Col lg={12}>
              <h2>Create a scenario</h2>
              <ScenarioTable
                scenarios={this.state.scenarios}
                table_updated={this.table_updated.bind(this)}
              />
            </Col>
            <Col lg={12}>
              <PingChart
                ref={this.ping_chart}
              />
            </Col>
            <Col lg={12}>
              <Button onClick={this.test_control.bind(this)}>{this.state.started ? (this.state.paused ? 'Continue' : 'Pause') : 'Start'}</Button> {' '}
            </Col>
            <Col xs={12} md={6}>
              <span><strong>Download</strong></span>
            </Col>
            <Col xs={12} md={6}>
              <span><strong>Upload</strong></span>
            </Col>
            <Col xs={6} md={4}>
              <span><strong>latency:</strong> {this.state.latest_measurement}</span>
              <br/>
              <span><strong>bw:</strong> {this.state.latest_bw_measurement}Mbps</span>
            </Col>
          </Row>
          <Row>
            <Col xs={12} md={6}>
              <span><strong>Download Measurements</strong></span>
              {Object.keys(this.state[MEASUREMENTS.DOWNLOAD]).map(id => (
                <BandwidthChart key={id}
                  data={this.state[MEASUREMENTS.DOWNLOAD][id]}
                  series_name={'Download'}
                  color_id={id}
                  title={this.format_title(this.state[MEASUREMENTS.DOWNLOAD][id], 'Download')}
                />
              ))}
            </Col>
            <Col xs={12} md={6}>
              <span><strong>Upload Measurements</strong></span>
              {Object.keys(this.state[MEASUREMENTS.UPLOAD]).map(id => (
                <BandwidthChart key={id}
                  data={this.state[MEASUREMENTS.UPLOAD][id]}
                  series_name={'Upload'}
                  color_id={id}
                  title={this.format_title(this.state[MEASUREMENTS.UPLOAD][id], 'Upload')}
                />
              ))}
            </Col>
          </Row>
        </Container>
      </>
    );
  }

  format_title(measurements, direction) {
    if (measurements.length === 0) {
      return "Loading...";
    }
    return `${measurements[0].label} ${direction}: (${measurements.length}/${measurements[0].num_measurements})`
  }

  async check_if_started_and_toggle_pause() {
    if (!this.state.started) {
      await this.setState({
        started: true,
        paused: false
      });
    } else {
      await this.setState({
        paused: !this.state.paused
      });
    }
  }

  async test_control() {
    await this.check_if_started_and_toggle_pause();
    if (this.state.paused) {
      if (this.current_scenario) {
        this.current_scenario.pause();
      }
      this.cancel_ping();
    } else {
      this.run_scenarios();
    }
  }

  async run_scenarios() {
    if (this.state.current_idx === this.state.scenarios.length) {
      console.log("Scenarios exhausted");
      return;
    }
    this.ping(AppConfiguration.PING_INTERVAL);
    this.current_scenario = this.state.scenarios[this.state.current_idx];
    while(await this.current_scenario.run()) {
      await this.setState({
        current_idx: (this.state.current_idx + 1)
      });
      if (this.state.current_idx === this.state.scenarios.length) {
        console.log("Scenarios finished");
        this.cancel_ping();
        return;
      }
      this.current_scenario = this.state.scenarios[this.state.current_idx];
    }
  }

  async collect_bandwidth_data(rate) {
    if (this.current_scenario.type === WaitScenario.TYPE){
      console.error("Received rate from a WaitScenario", rate);
      return;
    }
    await this.ensure_entry_exists();
    let scenario = this.current_scenario;
    let measurements = (scenario.type ===  DownloadScenario.TYPE) ?
      MEASUREMENTS.DOWNLOAD : MEASUREMENTS.UPLOAD;
    this.state[measurements][scenario.id].push({
      ...scenario,
      rate: rate
    });
    await this.setState({
      [measurements]: this.state[measurements]
    });
    console.log("received download data", rate);
  }

  async ensure_entry_exists() {
    let scenario = this.current_scenario;
    let measurements = (scenario.type ===  DownloadScenario.TYPE) ?
      MEASUREMENTS.DOWNLOAD : MEASUREMENTS.UPLOAD;
    if (!(scenario.id in this.state[measurements])) {
      this.state[measurements][scenario.id] = [];
      await this.setState({
        [measurements]: this.state[measurements]
      });
    }
  }

  collect_latency(latency) {
    if (this.state.paused) {
      console.warn("Skipping latency Measurement cuz paused...");
      return;
    }
    console.log('latency', latency);
    this.setState({
      latest_measurement: latency
    });

    this.ping_chart.current.add_point(
      latency,
      this.current_scenario.id,
      (this.current_scenario.type !== WaitScenario.TYPE)
    );
  }

  async ping(interval) {
    let start = Date.now();
    fetch("https://api.github.com/users/arshankhanifar")
      .then(res => res.json())
      .then(async res => {
        let end = Date.now();
        let latency = end - start;
        this.collect_latency(latency);
      });
    this.ping_timer = setTimeout(() => this.ping(interval), interval);
  }

  cancel_ping() {
    clearTimeout(this.ping_timer);
    this.ping_timer = null;
  }

}
