import React, {Component} from 'react';
import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';
import ColourProvider from "../utilities/colours";

export default class PingChart extends Component {
  chart_options = {
    chart: {
      zoomType: 'x',
      animation: {
        duration: 100
      }
    },
    title: {
      text: 'Latency'
    },
    subtitle: {
      text: document.ontouchstart === undefined ?
        'Click and drag in the plot area to zoom in' : 'Pinch the chart to zoom in'
    },
    xAxis: {
      type: 'datetime'
    },
    yAxis: {
      title: {
        text: 'Ping(ms)'
      },
      gridLineWidth: 0,
    },
    legend: {
      enabled: false
    },
    plotOptions: {
      area: {
        marker: {
          radius: 2
        },
        lineWidth: 1,
        states: {
          hover: {
            lineWidth: 1
          }
        },
        threshold: null
      }
    },

    series: []
  };


  constructor(props) {
    super(props);
    this.chartComponent = React.createRef();
  }

  render() {
    return (
      <>
        <HighchartsReact
          ref={this.chartComponent}
          highcharts={Highcharts}
          options={this.chart_options}/>
        <br/>

      </>
    );
  }

  series_lookup = {};

  get_series(id, coloured) {
    const chart = this.chartComponent.current.chart;
    if (!(id in this.series_lookup)) {
      this.series_lookup[id] = Object.keys(this.series_lookup).length;
      chart.addSeries({
        type: 'area',
        name: 'latency',
        data: [],
        color: coloured ? ColourProvider.get_colour(id) : 'rgb(120,120,120)'
      });
    }
    return chart.series[this.series_lookup[id]];
  }

  add_point(latency, id, coloured) {
    let series = this.get_series(id, coloured);
    series.addPoint([Date.now(), latency]);
  }
}
