import React, {Component} from 'react';
import Navbar from "react-bootstrap/Navbar";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";
import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';


export default class HighChartsSample extends Component {

  load_chart_options() {
    return {
      chart: {
        zoomType: 'x'
      },
      title: {
        text: 'USD to EUR exchange rate over time'
      },
      subtitle: {
        text: document.ontouchstart === undefined ?
          'Click and drag in the plot area to zoom in' : 'Pinch the chart to zoom in'
      },
      xAxis: {
        type: 'datetime'
      },
      yAxis: {
        title: {
          text: 'Exchange rate'
        }
      },
      legend: {
        enabled: false
      },
      plotOptions: {
        area: {
          //fillColor: {
          //  linearGradient: {
          //    x1: 0,
          //    y1: 0,
          //    x2: 0,
          //    y2: 1
          //  },
          //  stops: [
          //    [0, Highcharts.getOptions().colors[0]],
          //    [1, Highcharts.color(Highcharts.getOptions().colors[0]).setOpacity(0).get('rgba')]
          //  ]
          //},
          marker: {
            radius: 2
          },
          lineWidth: 1,
          states: {
            hover: {
              lineWidth: 1
            }
          },
          threshold: null
        }
      },
      series: [{
        type: 'area',
        name: 'series a',
        data: []
      },{
        type: 'area',
        name: 'series b',
        data: []
      }]
    };
  }

  constructor(props) {
    super(props);
    this.chartComponent = React.createRef();
  }

  render() {
    return (
      <>
        <Navbar bg="light">
          <Navbar.Brand href="#">KNP</Navbar.Brand>
        </Navbar>
        <Container>
          <Row>
            <Col>
              Hello, World!
              <HighchartsReact
                ref={this.chartComponent}
                highcharts={Highcharts}
                options={this.load_chart_options()}/>
            </Col>
          </Row>
          <Row>
            <Button onClick={this.redrawChart.bind(this)}>Redraw</Button> {' '}
            <Button onClick={this.addPoint.bind(this)}>Add Point</Button> {' '}
          </Row>
        </Container>
      </>
    );
  }

  addPoint() {
    const chart = this.chartComponent.current.chart;
    chart.series[0].addPoint([1514605600000, 2]);
  }

  redrawChart() {
    const chart = this.chartComponent.current.chart;
    chart.redraw();
  }

  componentDidMount() {
    const chart = this.chartComponent.current.chart;
    fetch('https://cdn.jsdelivr.net/gh/highcharts/highcharts@v7.0.0/samples/data/usdeur.json')
      .then(res => res.json())
      .then(data => {
        console.log(this.chartComponent);
        console.log('data0,100', data.splice(0,100));
        console.log('data100,200', data.splice(100,100));
        console.log('data200,300', data.splice(200,100));

        chart.addSeries({
          type: 'area',
          name: 'series a',
          //data: data.splice(0,600)
          data: data
        });
        chart.addSeries({
          type: 'area',
          name: 'series b',
          data: data.splice(100,100)
        });
        chart.addSeries({
          type: 'area',
          name: 'series c',
          data: data.splice(200,100)
        });
        chart.redraw();
      })
  }
}
