import Scenario from "./scenario";
import AppConfiguration from "../config";

export default class BandwidthScenario extends Scenario {
  static get BASE_URL() {
    return (AppConfiguration.CONFIG === "arshan") ?
      "https://worker.arshankhanifar.workers.dev":
      "https://speed.cloudflare.com";
  }

  constructor(num_measurements, file_size, num_streams, result_cb) {
    super();
    this.num_measurements = num_measurements;
    this.file_size = file_size;
    this.counter = 0;
    this.reset_controller();
    this.result_callback = result_cb;
    this.num_streams = num_streams;
  }

  reset_controller() {
    this.controller = new AbortController();
  }

  async run() {
    super.run();
    while (this.counter < this.num_measurements) {
      try {
        let rate = await this.measure();
        if (this.result_callback) {
          this.result_callback(rate);
        }
      } catch (e) {
        console.log(`download: ${this.counter} was cancelled`, e);
        return false;
      }
      this.counter += 1;
    }
    return true;
  }

  pause() {
    super.pause();
    this.controller.abort();
    this.reset_controller();
  }

  convert_to_mbps(r) {
    return r*1000/(1<<20)*8;
  }

  async measure() {
  }
}

